.App {
  text-align: center;
  margin-top: 2px;
  margin: auto;
  width: 80%;
  padding: 10px;
}
h1{
  font-family: cursive;
}
input{
  width: 220px;
  height: auto;
  padding: 1px;
  margin: 9px;
}
button{
  width: 120px;
  height: 33px;
  font-size: small;
  margin: 10px;
  border-radius: 22px;
  background-color: lightgreen;

  border: 0;
}
select{
  width: 155px;
  height: 33px;
  border-radius: 22px;
}

option{
  width: 155px;
  height: 33px;
  border-radius: 22px;
}
